import React from "react"
import "../../assets/css/layout.css"

function getField(data) {
	const contentArray = [];
	data.edges.forEach(item =>
		contentArray.push(
            <div className="col-4">
                <p>
                    <p_s>
                    <strong>{ item.node.datetext }</strong> - { item.node.location } - { item.node.description }
                    </p_s>
                </p>
            </div>
		)
	);
	return contentArray
}

export default getField
